.full-window {
    display: -ms-flexbox;
    display: flex;
    width: 100vw;
    height: 100vh;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.board {
    max-width: 100vw;
    max-height: 100vh;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: antiquewhite;
    position: relative;
}

.board canvas {
    max-width: 100%;
    max-height: 90vh;
    width: auto;
    height: 90vw;
}

.overlay {
    max-width: 100%;
    max-height: 90vh;
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
    height: 90vw;
}

.overlay.wall-overlay {
    z-index: 5;
    pointer-events: none;
}

.chess-item {
    position: absolute;
}

.player-chess {
    background: red;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.action-chess {
    background: yellow;
}

.click-point{
    background: brown;
    z-index: 10;
}



